import React, { Component } from 'react';
import './App.css';
import Todo from './Todo';

class App extends Component {
  render() {
    return (
      <><div id="content">

            <div id="title">
                <h2>ToDo App</h2>

            </div>


            <input id="inputBox" type="text" name="q" placeholder="Add your new Todo..." />

            <button className="btn" id="add"><i className="fa fa-plus"></i></button>


            <table id="todoList">

            </table>

        </div></>
    );
  }
}

export default App;
